export default {
	data() {
		return {
			mapCtx: '',
			latitude: 0,
			longitude: 0,
			time: "00:00:00",
			h: 0,
			m: 0,
			s: 0,
			sec: 0,
			aniPointsArr: [],
			aniPoints: [
			// 	{
			// 	latitude: 25.075309,
			// 	longitude: 102.712213,
			// }
			],
			aniPostions: [],
			aniPolyline: [],
			aniPolylineId: '',
			circles: [],
			km: 0,
			metters: 0,
			inter: null
		}
	},
	onLoad() {
		// 使用 my.createMapContext 获取 map 上下文
		this.mapCtx = uni.createMapContext('map');
		uni.showLoading();
		uni.authorize({
			scope: 'scope.userLocation',
			success: (res => {
				uni.getLocation()
			}),
			fail: (res => {
				console.log("原因:", res)
			})
		})
		uni.getLocation({
			type: 'gcj02',
			success: (res => {
				console.log('当前位置的经度：' + res.longitude);
				console.log('当前位置的纬度：' + res.latitude);
				console.log('位置精度：' + res.accuracy);
				console.log('水平位置精度：' + res.horizontalAccuracy);
				uni.hideLoading();
				this.latitude = res.latitude
				this.longitude = res.longitude
			}),
			fail: (res => {
				console.log("失败原因:", res)
				uni.hideLoading();
				uni.alert({
					title: '定位失败'
				});
			})
		});
	},
	computed: {
		speed() {
			if (this.metters) {
				const dateFamt = n => n > 10 ? n : '0' + n
				var time, route, speed, search, rep, fltNum, fltStr, m, s, speedStr
				time = (this.h * 60) + this.m + (this.s / 60)
				route = this.metters
				speed = time / route
				search = speed.toString()
				rep = search.replace('.', "'")
				fltNum = search.search("'")
				fltStr = search.slice(fltNum)
				m = parseInt(speed)
				s = Number(fltStr) * 6
				speedStr = `${m}'${dateFamt(s)}"`
			}
			return speedStr ? speedStr : `0'00"`
		}
	},
	methods: {
		//更新缩放比例
		scaleChange() {
			this.scale = 18
		},

		GetDistance(lat1, lng1, lat2, lng2) {
			const Rad = d => d * Math.PI / 180 //经纬度转换成三角函数中度分表形式。
			var radLat1 = Rad(lat1);
			var radLat2 = Rad(lat2);
			var a = radLat1 - radLat2;
			var b = Rad(lng1) - Rad(lng2);
			var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a / 2), 2) + Math.cos(radLat1) * Math.cos(radLat2) * Math.pow(
				Math.sin(b / 2), 2)));
			s = s * 6378.137;
			s = (Math.round(s * 10000) / 10000) //输出为公里
			return s
		},
		// 开始跑步
		start() {
			const dateFamt = n => n > 10 ? n : '0' + n
			if (this.intervalId != null) return;
			this.intervalId = setInterval(() => {
				//实时更新定位
				uni.getLocation()
				//计时器 累加时间 跑步时间
				this.sec++;
				let date = new Date(0, 0);
				date.setSeconds(this.sec);
				let h = date.getHours(),
					m = date.getMinutes(),
					s = date.getSeconds();
				this.h = h
				this.m = m
				this.s = s
				this.time = dateFamt(h) + ":" + dateFamt(m) + ":" + dateFamt(s);
			}, 1000);
			this.setPoly()
		},
		// 停止跑步
		stop() {
			clearInterval(this.intervalId)
			clearInterval(this.inter)
			this.intervalId = this.inter = null
		},
		//结束跑步
		clearMsg() {
			this.time = "00:00:00"
			this.metters = 0,
				clearInterval(this.intervalId)
			clearInterval(this.inter)
			this.intervalId = this.inter = null
		},
		setPoly() {
			console.log('当前位置的经度2：' + this.longitude);
			console.log('当前位置的纬度2：' + this.latitude);
			// let hash = ;
			this.aniPointsArr.push({
				latitude: this.latitude,
				longitude: this.longitude
			})
			for (var i = 1; i < this.aniPointsArr.length; i++) {
				if (this.aniPointsArr[i] != this.aniPoints[this.aniPoints.length - 1]) {
					this.aniPoints.push(this.aniPointsArr[i]);
				}
			}
			console.log("this.aniPoints:", this.aniPoints);
			for (var i = 0; i < this.aniPoints.length; i++) {
				this.aniPolylineId = this.aniPoints[i].__ob__.dep.id
			}
			console.log("this.aniPolylineId:", this.aniPolylineId);

			// this.aniPostions.push({
			// 	points: this.aniPointsArr
			// })
			// 
			this.aniPolyline = [{
				points: this.aniPoints,
				color: '#0ff',
				width: 10,
				dottedLine: false,
				iconWidth: 10,
			}]
			console.log("this.aniPolyline:", this.aniPolyline);

			if (this.inter != null) return
			this.inter = setInterval(() => {
				this.mapCtx.smoothMovePolyline({
					polylineId: this.aniPolylineId,
					points: this.aniPoints,
					color: '#00ff00',
					width: 6,
					dottedLine: false,
					iconWidth: 10,
					duration: 500,
					colorList: true
				});
				this.mapCtx.updateComponents({
					polyline: this.aniPolyline,
				});
				// uni.getLocation({
				// 	success: (res) => {
				// 		let covers, points, len, len_index
				// 		this.circles.push({
				// 			latitude: res.latitude,
				// 			longitude: res.longitude,
				// 			color: "#000",
				// 			fillColor: "#000",
				// 			radius: 30,
				// 			strokeWidth: 5
				// 		});
				// 		this.markers.push({
				// 			latitude: res.latitude,
				// 			longitude: res.longitude,
				// 			iconPath: '../../static/redPoint.png'
				// 		});
				// this.polyline[0].points.push({
				// 	latitude: this.latitude,
				// 	longitude: this.longitude
				// })
				// points = this.polyline[0].points
				// len = points.length
				// len_index = points.length - 1
				// //当数组的长度大于2 从右往左取
				// if (len > 2) {
				// 	let lat1, lat2, lng1, lng2
				// 	lat1 = points[len_index - 1]['latitude']
				// 	lng1 = points[len_index - 1]['longitude']
				// 	lat2 = points[len_index]['latitude']
				// 	lng2 = points[len_index]['longitude']
				// 	this.km = (this.GetDistance(lat1, lng1, lat2, lng2)).toFixed(2)
				// 	this.metters += Number(this.km);
				// }
				// 	}
				// });
			}, 500)
		},
	}
}
